<template>
  <div id="quill-upload">
    <!-- -->
    <el-upload
      class="avatar-uploader"
      :action="imgUploadUrl"
      name="file"
      :on-success="uploadSuccess"
      :on-error="uploadError"
      :before-upload="beforeUpload">
    </el-upload>
    <!---->
    <el-row v-loading="quillUpdateImg" :style="editHeight">
      <quill-editor
        ref="myQuillEditor"
        v-model="editerContent"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
        @ready="onEditorReady($event)"
      >
      </quill-editor>
    </el-row>
  </div>
</template>

<script>
  // import 'quill/dist/quill.core.css'
  // import 'quill/dist/quill.snow.css'
  // import 'quill/dist/quill.bubble.css'
  import {quillEditor} from 'vue-quill-editor'
  // 
  const toolbarOptions = [
    ['bold', 'italic', 'underline', 'strike'], // toggled buttons
    ['blockquote', 'code-block'],
    [{'header': 1}, {'header': 2}], // custom button values
    [{'list': 'ordered'}, {'list': 'bullet'}],
    [{'script': 'sub'}, {'script': 'super'}], // superscript/subscript
    [{'indent': '-1'}, {'indent': '+1'}], // outdent/indent
    [{'direction': 'rtl'}], // text direction
    // [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
    // [{'header': [1, 2, 3, 4, 5, 6, false]}],
    [{'color': []}, {'background': []}], // dropdown with defaults from theme
    [{'font': []}],
    [{'align': []}],
    ['link', 'image']
  ]
  export default {
    name: 've-editer',
    data () {
      return {
        quillUpdateImg: false,
        editerContent: '', // 
        editorOption: {
          placeholder: '',
          theme: 'snow', // 'snow' or 'bubble'
          modules: {
            toolbar: {
              container: toolbarOptions, // 
              handlers: {
                'image': function (value) {
                  if (value) {
                    document.querySelector('#quill-upload input').click()
                  } else {
                    this.quill.format('image', false)
                  }
                }
              }
            }
          }
        }
      }
    },
    components: {quillEditor},
    props: {
      value: {
        default: ''
      },
      height: {
        type: String,
        default: '300px'
      },
      imgUploadUrl: {
        type: String,
        default: '/api/upload/do-upload'
      }
    },
    computed: {
      editHeight () {
        return {height: this.height.indexOf('%') !== -1 ? this.height : (this.height - 21 + 'px')}
      }
    },
    watch: {
      value: {
        handler (newVal) {
          this.editerContent = newVal
        },
        immediate: true
      },
      editerContent (val) {
        this.$emit('input', val)
      }
    },
    methods: {
      onEditorBlur (e) { // 
        this.$emit('blur', e)
      },
      onEditorFocus (e) { // 
        this.$emit('focus', e)
      },
      onEditorChange (e) { // 
        // this.$emit('input', this.content)
        this.$emit('change', e)
      },
      onEditorReady (e) {
        this.$emit('ready', e)
      },
      beforeUpload () { // 
        this.quillUpdateImg = true
      },
      uploadSuccess (res) { // // res
        // 
        let quill = this.$refs.myQuillEditor.quill
        // 
        if (res.code === 200 && res.data !== null) {
          // 
          let length = quill.getSelection().index
          //   res.info
          quill.insertEmbed(length, 'image', `${res.data.host}/${res.data.name}`)
          // 
          quill.setSelection(length + 1)
        } else {
          this.$message.error('')
        }
        // loading
        this.quillUpdateImg = false
      },
      uploadError () { // 
        this.quillUpdateImg = false
        this.$message.error('')
      }
    }
  }
</script>

<style lang="scss">
  @import '../../node_modules/quill/dist/quill.core.css';
  @import '../../node_modules/quill/dist/quill.snow.css';
  @import '../../node_modules/quill/dist/quill.bubble.css';

  #quill-upload {
    // height: 300px;
    min-width: 688px;
    .avatar-uploader{
      display: none;
    }
    .el-row {
      height: 100%;
      .quill-editor {
        height: 100%;
      }
      .ql-toolbar {
        text-align: left;
      }
      .ql-toolbar.ql-snow .ql-formats {
        margin-right: 0px;
      }
    }
    .ql-toolbar.ql-snow + .ql-container.ql-snow {
      height: calc(100% - 42px);
    }
  }
</style>
